@charset "UTF-8";
.add[data-v-bf4b54dc] {
  display: none;
}
.none[data-v-bf4b54dc] {
  display: none;
}
.chooseShow[data-v-bf4b54dc] {
  display: none;
}
.pos-waring[data-v-bf4b54dc] {
  /* 隐藏滚动条 */
  position: absolute;
  left: 100px;
  top: 45px;
  height: 300px;
  width: 870px;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: #fff;
  z-index: 99999;
}
.pos-waring .table[data-v-bf4b54dc] {
    /*此元素会作为块级表格来显示（类似 <table>），表格前后带有换行符。*/
    display: table;
    border-collapse: collapse;
    margin: 0 auto;
}
.pos-waring .table .content[data-v-bf4b54dc]::-webkit-scrollbar {
      display: none;
}
.pos-waring .table .content[data-v-bf4b54dc] {
      width: 870px;
      height: 265px;
      padding-left: 10px;
      overflow: auto;
}
.pos-waring .table .table-caption div[data-v-bf4b54dc] {
      display: inline-block;
      margin: 10px 10px 0px 0px;
}
.pos-waring .table .table-caption[data-v-bf4b54dc] {
      /*此元素会作为一个表格标题显示（类似 <caption>）*/
      display: table-caption;
      margin: 0;
      font-size: 16px;
      text-align: right;
}
.pos-waring .table .table-caption .check-all[data-v-bf4b54dc] {
        font-weight: bold;
        color: #000;
        cursor: pointer;
}
.pos-waring .table .table-caption .uncheck[data-v-bf4b54dc] {
        font-weight: bold;
        color: rgba(0, 0, 0, 0.35);
        cursor: pointer;
}
.pos-waring .table .table-caption .del[data-v-bf4b54dc] {
        font-weight: bold;
        color: #f10b0b;
        cursor: pointer;
}
.pos-waring .table .table-caption .undel[data-v-bf4b54dc] {
        font-weight: bold;
        color: rgba(241, 11, 11, 0.35);
        cursor: pointer;
}
.pos-waring .table .table-header-group[data-v-bf4b54dc] {
      /*此元素会作为一个或多个行的分组来显示（类似 <thead>）。*/
      display: table-header-group;
      background: #eee;
      font-weight: bold;
}
.pos-waring .table .table-column-group[data-v-bf4b54dc] {
      /*此元素会作为一个或多个列的分组来显示（类似 <colgroup>）。*/
      display: table-column-group;
}
.pos-waring .table .table-column-group .table-column-1[data-v-bf4b54dc] {
        display: table-column;
        width: 50px;
}
.pos-waring .table .table-column-group .table-column[data-v-bf4b54dc] {
        /*此元素会作为一个单元格列显示（类似 <col>）*/
        display: table-column;
        width: 200px;
}
.pos-waring .table .table-row-group[data-v-bf4b54dc] {
      /*此元素会作为一个或多个行的分组来显示（类似 <tbody>）。*/
      display: table-row-group;
}
.pos-waring .table .table-row-group .table-row[data-v-bf4b54dc]:hover {
        background: #f6f6f6;
        color: black;
        font-weight: bold;
}
.pos-waring .table ul[data-v-bf4b54dc] {
      list-style: none;
      text-align: center;
}
.pos-waring .table .table-row[data-v-bf4b54dc] {
      /*此元素会作为一个表格行显示（类似 <tr>）。*/
      display: table-row;
}
.pos-waring .table .table-cell[data-v-bf4b54dc] {
      /*此元素会作为一个表格单元格显示（类似 <td> 和 <th>）*/
      display: table-cell;
      padding: 0 5px;
      border: 1px solid #ccc;
}
